<template>
  <div class="dialog-root" v-if="show">
    <div class="dialog-view">
      <div class="dialog-title">出错了</div>
      <div class="dialog-content">{{ content }}</div>
      <div class="dialog-foot">
        <div class="dialog-confirm" @click="confirm">
          我知道了{{ number ? `(${number}s)` : "" }}
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    dialogShow: {
      value: Boolean,
      default: () => false,
    },
  },
  data() {
    return {
      show: false,
      Interval: null,
      number: 60,
      content: "弹窗内容弹窗内容弹窗内容弹窗内容弹窗内容弹窗内容",
    };
  },
  watch: {
    dialogShow: {
      handler(newVal) {
        this.show = newVal;
        newVal && this.countDown();
      },
      immediate: true,
    },
  },
  beforeDestroy() {
    this.Interval && clearInterval(this.Interval);
  },
  methods: {
    countDown() {
      this.Interval && clearInterval(this.Interval);
      this.number = 60;
      this.Interval = setInterval(() => {
        this.number = this.number - 1;
        if (this.number <= 0) {
          this.show = false;
          clearInterval(this.Interval);
        }
      }, 1000);
    },
    close() {
      this.show = false;
      this.$emit("close");
    },
    confirm() {
      this.show = false;
      this.$emit("confirm");
    },
  },
};
</script>
<style scoped lang="scss">
.dialog-root {
  z-index: 99;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  .dialog-view {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 288px;
    min-height: 179px;
    transform: translate(-50%, -50%);
    background: rgba(0, 0, 0, 0.95);
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 45px 30px 32px;

    .dialog-close {
      cursor: pointer;
      width: 18px;
      height: 18px;
      position: absolute;
      right: 19px;
      top: 19px;
    }

    .dialog-icon {
      width: 101px;
      height: 101px;
      margin-bottom: 20px;
    }

    .dialog-title {
      font-family: "PingFang SC";
      font-style: normal;
      font-weight: 400;
      text-align: center;
      font-size: 18px;
      line-height: 26px;
      color: #ffffff;
    }

    .dialog-content {
      display: inline;
      text-align: left;
      margin: 15px auto;
      font-weight: 500;
      font-size: 14px;
      line-height: 22px;
      color: #b3b3b3;
    }

    .dialog-foot {
      width: 100%;
      padding: 0 10px;
      display: flex;
      justify-content: center;
      align-items: center;

      .dialog-confirm {
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 140px;
        height: 37px;
        border-radius: 2px;
        color: #ffffff;
        border: 1px solid #ffffff;
      }
      .dialog-confirm:active {
        color: #4a4a4a;
        opacity: 0.95;
      }
    }
  }
}
</style>
